import { Line } from 'react-chartjs-2';

import React, { useContext, useEffect } from 'react';
import BigNumber from 'bignumber.js';

import {Card, Row, Col, Divider, useMediaQuery} from '@geist-ui/react'
import { StatsRowItem } from '../common/index';
import {formatBN} from "../../utils/number";
import {EpochContext, ContextType } from "../../store/EpochStore";
import { createVoidZero } from 'typescript';

function fm(bn){
  let num = (formatBN(bn, 2))
  return num;
}

const MomentumHeader = ({ user }: {user: string}) => {
const downLG = useMediaQuery('lg', { match: 'down' })
const upSM = useMediaQuery('sm', { match: 'up' })
const { regulations, updateRegulations} = React.useContext(EpochContext) as ContextType;
let regs = regulations.reverse().slice(-15)
const prices = regs.map((reg) => Number(reg.price.toNumber().toFixed(2)));
const momentumPrice = regs.map((reg) =>  reg.momentumPrice ? Number(reg.momentumPrice.toNumber().toFixed(2)): 0);
const labels = regs.map((x) => x.epoch)
// @ts-ignore
const nextChange = momentumPrice.length > 1 ? (momentumPrice[momentumPrice.length-1].toFixed(2) > prices[momentumPrice.length-1].toFixed(2) ? 'Contraction' : 'Expansion' ) : ''
const price_data = {
    labels: labels,
    datasets: [
      {
        label: 'Price ($)',
        data: prices,
        backgroundColor: 'rgba(75,192,192,0.4)',
        borderColor: 'rgba(75,192,192,1)',
        borderCapStyle: 'butt',
        borderDash: [],
        borderDashOffset: 0.0,
        borderJoinStyle: 'miter',
        pointBorderColor: 'rgba(75,192,192,1)',
        pointBackgroundColor: '#fff',
        pointBorderWidth: 1,
        pointHoverRadius: 5,
        pointHoverBackgroundColor: 'rgba(75,192,192,1)',
        pointHoverBorderColor: 'rgba(220,220,220,1)',
        pointHoverBorderWidth: 2,
        pointRadius: 3,
      },
      {
        label: 'Momentum ($)',
        borderDash: [5,15],
        backgroundColor: 'rgba(255,99,132,0.2)',
		borderColor: 'rgba(255,99,132,1)',
		borderWidth: 1,
		hoverBackgroundColor: 'rgba(255,99,132,0.4)',
		hoverBorderColor: 'rgba(255,99,132,1)',
        data: momentumPrice
      }
    ]
  };
const options = {
  scales: {
    yAxes: [{
      display: downLG ? false :true,
      position: 'right',
      ticks: {
        callback: function(label, index, labels) {
            return '$' + label;
        }
    },
    }],
    xAxes: [{
      display: downLG ? false :true,
      ticks: {
        callback: function(label, index, labels) {
            return 'Ep ' + label;
        }
      }
    }]
  }
}

useEffect(() => {
    updateRegulations();
},[user])
return (
    <Card style={{display: 'flex'}}>
        {upSM ? 
        <Row>
            <Col span={20}>
                <Line height={65}
                        legend={{display: false}}
                        data={price_data}
                        options={options}/> 
            </Col> 

            <Col span={4}>
                <Card type="success" shadow style={{height: '100%', fontSize: '12px'}}>
                    {/* @ts-ignore */}
                    Momentum Price <span style={{color: '#000', fontSize: '12px'}}>${momentumPrice[momentumPrice.length -1]?.toFixed(2) || 0}</span>
                    <Card.Footer>
                    Next Epoch: 
                    <Divider/>
                    <span style={{color: '#000', fontSize: '12px'}}> {nextChange}</span>
                    </Card.Footer>
                </Card>
            </Col>
        </Row> : 
        <Card type="success" shadow style={{fontSize: '12px'}}>
            {/* @ts-ignore */}
            Momentum Price: <span style={{color: '#000', fontSize: '12px'}}>${momentumPrice[momentumPrice.length -1]?.toFixed(2) || 0}</span>
            <Card.Footer>
            <div>Next Epoch: </div>
            <Divider/>
            <div style={{color: '#000', fontSize: '12px'}}> {nextChange}</div>
            </Card.Footer>
        </Card>}
    </Card>
)
};


export default MomentumHeader;
